<template>
  <app-container>
    <app-header title="消息" @click-right="setState">
      <span slot="right">全部已读</span>
    </app-header>

    <van-tabs
      v-if="showTab"
      v-model="activeTab"
      color="#2c5cf7"
      :line-width="16"
      @change="tabsChange"
    >
      <van-tab
        v-for="item in tabsArray"
        :key="item.product"
        :title="item.name"
        :name="item.product"
      />
    </van-tabs>

    <div class="list">
      <van-pull-refresh
        v-model="isLoading"
        @refresh="onRefresh"
        class="refresh-wrapper"
      >
        <empty-page
          emptyMsg="暂无消息"
          :showEmpty="!list.length"
          :loadingPage="this.loading"
        />
        <van-list
          v-model="loadingMore"
          :finished="finished"
          finished-text="— 干工程是有底线的 —"
          :immediate-check="false"
          @load="loadMore"
        >
          <div
            v-for="(item, k) in list"
            :key="k"
            class="block"
            @click="open(item)"
          >
            <div>
              <div class="toper">
                <span class="type">
                  <img :src="item.icon || ''" />
                  {{ item.type }}
                </span>
                <span class="time">{{ item.date }}</span>
              </div>
              <div class="inner">
                <div class="title" :class="{ unread: !item.isRead }">
                  {{ item.title }}
                </div>
                <div class="message">{{ item.message }}</div>
              </div>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
  </app-container>
</template>

<script>
import { PullRefresh, List } from 'vant';
import RouterService from '@services/router-service';
import MessageService from '@services/message-service';
import Config from '@src/config/page/message-config';
import { showZhkfModlue } from '@/src/config/sub-app-white-list';

let totalPage = 0;
const productCodeName = {
  gcxt: '工程协同',
  zhkf: '智慧客服',
  zjapp: '工程在线',
};
export default {
  name: 'MessageIndex',
  components: {
    [PullRefresh.name]: PullRefresh,
    [List.name]: List,
  },
  data() {
    return {
      loading: false, // 是否正在加载数据，防止重复请求
      currPage: 1,
      isLoading: false,
      loadingMore: false,
      finished: false,
      list: [],
      activeTab: '',
      products: [], // 用户拥有的产品权限，写在data是为了切换用户登录后重新计算
    };
  },

  activated() {
    let PRODUCTS = [...window.GLOBAL_ENV.PRODUCTS];
    // 不加载智慧客服代码时，若有智慧客服产品的权限，也不展示
    if (!showZhkfModlue()) {
      const index = PRODUCTS.findIndex(item => item === 'zhkf');
      if (index >= 0) {
        PRODUCTS.splice(index, 1);
      }
    }
    this.products = PRODUCTS;
    if (!this.products.includes(this.activeTab)) {
      if (this.products.includes('gcxt')) {
        this.activeTab = 'gcxt';
      } else if (this.products.includes('zhkf')) {
        this.activeTab = 'zhkf';
      } else if (this.products.includes('zjapp')) {
        this.activeTab = 'zjapp';
      } else {
        this.activeTab = '';
      }
    }
    this.init();
  },

  computed: {
    tabsArray() {
      const arr = [];
      Object.keys(productCodeName).forEach(code => {
        if (this.products.find(item => item === code)) {
          arr.push({ product: code, name: productCodeName[code] });
        }
      });
      return arr;
    },
    // 有多个产品时显示tab切换
    showTab() {
      return this.tabsArray ? this.tabsArray.length > 1 : false;
    },
  },

  methods: {
    init() {
      this.loading = false;
      totalPage = 0;
      this.currPage = 1;
      this.list = [];
      this.load();
    },
    load() {
      // 质检目前是写死的配置，没有接口
      if (this.activeTab === 'zjapp') {
        this.list = [Config['zjapp'].daily_paper];
        this.finished = true;
        return;
      }

      if (this.loading) {
        return Promise.reject(false);
      }

      this.loading = true;
      return MessageService.getMessageList(this.currPage, this.activeTab)
        .then(res => {
          totalPage = res.totalPage;
          console.log('totalPage ====> ', totalPage);
          if (this.currPage === 1) {
            this.list = res.list;
          } else {
            this.list = this.list.concat(res.list);
          }
          this.currPage++;
          this.loading = false;
          this.finished =
            !!(totalPage && this.currPage > totalPage) || totalPage === 0;
          console.log('this.list.length ===> ', this.list.length);
        })
        .catch(err => {
          console.error(err);
          this.loading = false;
          return Promise.reject('获取失败');
        });
    },

    onRefresh() {
      if (this.activeTab === 'zjapp') {
        this.isLoading = false;
        return false;
      }
      if (this.loading) {
        return false;
      }
      this.isLoading = true;
      this.currPage = 1;
      this.load()
        .then(() => {
          this.isLoading = false;
        })
        .catch(() => {
          this.isLoading = false;
        });
    },

    loadMore() {
      this.finished =
        !!(totalPage && this.currPage > totalPage) || totalPage === 0;

      if (this.finished) {
        this.loadingMore = false;
        return false;
      }

      console.log('this.load() ===> ', this.load());

      return this.load()
        .then(() => {
          this.finished = true; // this.list.length >= res.count;
          this.loadingMore = false;
        })
        .catch(() => {
          this.finished = true;
          this.loadingMore = false;
        });
    },

    setState() {
      if (this.activeTab !== 'zjapp') {
        return MessageService.setState([], this.activeTab).then(() => {
          this.list.forEach(v => {
            v.isRead = true;
          });
        });
      }
    },

    open(item) {
      if (this.activeTab === 'gcxt') {
        item.isRead = true;
        MessageService.setState([item.id], this.activeTab);
      }
      return RouterService.goto(item);
    },
    tabsChange() {
      this.init();
    },
  },
};
</script>

